@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Padauk:wght@700&display=swap'); */


body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: linear-gradient(to bottom,  #2e968b 50%, #42ddce 100%);

    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 100vh;
    font-family: 'Orbitron', sans-serif;


}
button {
    cursor: pointer;
}


.Tilt {
    background-image: linear-gradient( 135deg, #90F7EC 10%, #32CCBC 100%);
    margin-top: -90px;
    margin-bottom: 100px;
    }
.form {
    width: 700px;
    background-image: linear-gradient(to left, #90f7ec 10%, #32ccbc 100%);
    background-size: 4px 2px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
    border: none;
    margin-bottom: 14em;
  }
  
  .form input:focus,
   textarea:focus {
    outline: none;
    border: none;
  }
  
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;

}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.particles {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
